import PropTypes from 'prop-types'

const Form = ( props ) => {
  const { onSubmit, onFirstNameChange, onLastNameChange, onEmailChange, onMessageChange, data } = props
  const { sent, firstName, lastName, email, message, buttonText } = props.state

  if ( !sent ) {
    return (
      <section>
        <div className={`container flex`}>
          <div className={`col`}>
            {data && (
              <p>Found stuff</p>
            )}
            <form className="contact-form" onSubmit={onSubmit}>
              <fieldset>
                <label htmlFor="first_name">First Name</label>
                <input 
                  onChange={onFirstNameChange} 
                  type="text" 
                  name="first_name" 
                  id="first_name"
                  placeholder="First Name"
                  value={firstName}
                  inputMode={`text`}
                  required />
              </fieldset>
              <fieldset>
                <label htmlFor="last_name">Last Name</label>
                <input 
                  type="text" 
                  name="last_name" 
                  id="last_name"
                  onChange={onLastNameChange}
                  value={lastName}
                  placeholder="Last Name"
                  inputMode={`text`}  
                  required />
              </fieldset>
              <fieldset>
                <label htmlFor="email">Email</label>
                <input 
                  type="email" 
                  name="email" 
                  id="email"
                  onChange={onEmailChange}
                  placeholder="your@email.com"
                  value={email} 
                  inputMode={`email`}
                  required />
              </fieldset>
              <fieldset>
                <label htmlFor="message">Message</label>
                <textarea 
                  name="message" 
                  id="message"
                  onChange={onMessageChange}
                  value={message}
                  inputMode={`text`}
                  required>
                </textarea>
              </fieldset>
              <div className="button-wrapper">
                <button type="submit" className="btn">{buttonText}</button>
              </div>
            </form>
          </div>
          <div className={`col`}>
            test
          </div>
        </div>
      </section>
    )
  } else {
    return (
      <div>
        <h4>Message Received!</h4>
        <p>Thanks for you interest! I will contact you as soon as possible.</p>
      </div>
    )
  }
}

Form.propTypes = {
  onFirstNameChange: PropTypes.func,
  onLastNameChange: PropTypes.func,
  onMessageChange: PropTypes.func,
  onEmailChange: PropTypes.func,
  onSubmit: PropTypes.func,
  state: PropTypes.any,
}

export default Form